
<style lang="less" scoped>
  .header {
    display: flex;
    height: 2.5rem;
    background-color: #fff;
    &>div {
      flex: 2;
      padding-left: 1.25rem;
      height: 2.5rem;
      vertical-align: middle;
      &>img {
        width: 1.25rem;
        height: 1.875rem;
        vertical-align: middle;
      }
    }
    &>span {
      flex: 8;
      text-indent: 3em;
      vertical-align: middle;
      line-height: 1.875rem;
    }
  } // 头部
  .item_content_flex {
    display: flex;
    height: 6.25rem;
    background-color: #fff;
    &>div {
      box-sizing: border-box;
      height: 6.25rem;
      overflow: hidden;
    }
    div:nth-child(1) {
      flex: 4;
      padding: .3125rem;
      text-align: center;
      img {
        height: 100%;
      }
    }
    div:nth-child(2) {
      flex: 5;
    }
    div:nth-child(3) {
      flex: 1;
      img {
        width: 1.25rem;
        height: 1.875rem;
        padding-top: 50%;
      }
    }
  }
  .item_detail {
    h1 {
      font-size: 1rem;
      padding: .625rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    p {
      padding: .125rem .3125rem .125rem .625rem;
      b {
        margin-left: .3125rem;
      }
    }
  }
  .direction{
    transform: scale(0.7);
  }
  .noWrapper{
    text-align: center;
    &>img{
      width: 100px;
      margin: 40px 0 20px 0;
    }
    color: #aaa;
    font-size: 12px;
  }
</style>

<template>
  <div>
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <section>
      <div class="item_content_flex" @click="enterMyWorkDetail(item)" v-for="(item, index) in live" :key="index">
        <div><img v-lazy="item.coverImage" alt="课程展示图片"></div>
        <div class="item_detail">
          <h1>{{item.title}}</h1>
          <p><span>老师布置:</span><b>{{item.taskTotal}}</b>份</p>
          <p><span>学员提交:</span><b>{{item.jobTotal}}</b>份</p>
        </div>
        <div>
          <img class="direction" :src="direction" alt="查看课程详情">
        </div>
      </div>
    </section>
    <p class="noWrapper" v-if="!live.length"><img :src="noWrapperImg" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
  </div>
</template>

<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        live: [],
        centerTitle: '我的作业',
        direction: require('../../assets/img/je3skwbl.png'),
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        noWrapperImg: require('../../assets/img/da.png')
      }
    },
    methods: {
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/courses/mylivebroadcast_valid', {
            params: {
              page: this.page
            }
          }).then((res) => {
            if (this.pagination) {
              if (res.data.result && res.data.result.length > 0) {
                this.live = this.live.concat(res.data.result)
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有更多课程了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
              }
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      },
      back() {
        this.$router.back(-1)
      },
      enterMyWorkDetail(item) {
        this.$router.push({
          name: 'myWorkDetail',
          params:{
            courseId: item.courseId,
            title: item.title
          }
        })
      }
    },
    mounted() {
      window.onscroll = ()=>{
        this.dropDown()
      }
      this.axios.get('/api/courses/mylivebroadcast_valid',{
        params: {
          page: this.page
        }
      }).then(res => {
        this.live = res.data.result
      }).catch(err => {
        console.log(err)
      })
    }
  }
</script>





